    width:100%;
    height:147px;
    background:rgba(0,0,0,1);
    -webkit-box-shadow:0px -2px 0px 0px rgba(0, 0, 0, 1);
            box-shadow:0px -2px 0px 0px rgba(0, 0, 0, 1);
}
.footer-page div p {
    width:54px;
    height:26px;
    font-size:27px;
    font-family:PingFang SC;
    font-weight:400;
    color:rgb(228, 224, 22);
    margin-left: 97px;
    margin-top: 5px;
}
.footer-page div img{
    margin-top: 34px;
}
.footer-page div{
    float: left;
    height: 147px;
    width: 20%;
    text-align: center
}


